import { React, useEffect, useState } from "react";
// react router dom
import { BrowserRouter as Router } from "react-router-dom";
import Direccionamiento from "./Direccionamiento";
import Header from "./partials/Header";
import Footer from "./partials/Footer";


function App(){
    document.title = "Eafy Heroku"
    const [loggedIn, setLoggedIn] = useState(false);
    const [userEmail, setUserEmail] = useState("");
    const [userContrasea, setUserContrasea] = useState("");

    
    useEffect(() => {
        console.log("BUSCANDO DATOS");
        if (window.localStorage.getItem("userEmail") && window.localStorage.getItem("userContrasea")) {
            console.log("VALOR ENCONTRADO EMAIL");
            const emailData = window.localStorage.getItem("userEmail");
            console.log(emailData);
            setUserEmail(JSON.parse(emailData));
            console.log("VALOR ENCONTRADO CONTRASEA");
            const contraseaData = window.localStorage.getItem("userContrasea");
            console.log(contraseaData);
            setUserContrasea(JSON.parse(contraseaData));
            console.log("imprimeindo loggedin", loggedIn);
        } else {
            setUserEmail(null);
            setUserContrasea(null);
            console.log("imprimeindo loggedin", loggedIn);
        }
    }, []);
    
    useEffect(() => {
        console.log("imprimiendo info de usuario", userEmail," : ", userContrasea);
        if (userEmail && userContrasea) {
            window.localStorage.setItem('userEmail', JSON.stringify(userEmail));
            window.localStorage.setItem('userContrasea', JSON.stringify(userContrasea));
        } else {
            /*
            const emailData = window.localStorage.getItem("userEmail");
            console.log(emailData);
            setUserEmail(JSON.parse(emailData));

            const contraseaData = window.localStorage.getItem("userContrasea");
            console.log(contraseaData);
            setUserContrasea(JSON.parse(contraseaData));
            */
        }
    }, [userEmail, userContrasea]);

    useEffect(() => {
        setLoggedIn(JSON.parse(window.localStorage.getItem('loggedIn')));
        console.log("imprimiendo loggedIn", loggedIn);
    }, []);
    
    useEffect(() => {
        window.localStorage.setItem('loggedIn', loggedIn);
        console.log("imprimendo loggedIn", loggedIn)
    }, [loggedIn]);

    const logOut = () => {
        setLoggedIn(false);
        setUserEmail("");
        setUserContrasea("");
        window.localStorage.setItem('userEmail', "");
        window.localStorage.setItem('userContrasea', "");
    }
      

    return (
        <Router>
            <div>
                <Header loggedIn={loggedIn} setLoggedIn={setLoggedIn} logOut={logOut}/>
                    <Direccionamiento loggedIn={loggedIn} setLoggedIn={setLoggedIn}
                                        userEmail={userEmail} setUserEmail={setUserEmail}
                                        userContrasea={userContrasea} setUserContrasea={setUserContrasea}
                                        />
                <Footer loggedIn={loggedIn} setLoggedIn={setLoggedIn} />
            </div>
        </Router>
    );
}

export default App;